div.categories {
    /* background-color: yellowgreen; */
    padding: 10px;
}

div.categories ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

div.categories li {
    font-size: 1.5rem;
    padding: 10px;
    border: 1px solid black;
    margin: 10px;
    cursor: pointer;
}

div.categories ul li.active {
    font-weight: bolder;
}

div.categories li:hover {
    background-color: black;
    color: white;
}