div.productsList {
    width: 70%;
    padding: 10px;
    color: black;
}

div.productsList ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

div.productsList li {
    font-size: 1.5rem;
    padding: 5px;
    border: 1px solid black;
    margin: 10px;
    width: 25%;
    overflow: hidden;
    /* cursor: pointer; */
}

div.productsList li:hover {
    background-color: black;
    color: white;
}

div.productsList ul img {
    max-width: 10vh;
    /* margin-right: 20px; */
}

div.productsList ul button {
    font-size: 1.5rem;
    padding: 10px;
    /* max-height: 50px; */
    cursor: pointer;
}

div.productsList ul button:hover {
    background-color: lightgray;
}

div.productsList ul li p:last-child {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}