div.cart {
    /* background-color: aquamarine; */
    max-width: 70%;
    margin: 0 auto;
    font-size: 1.5rem;
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

table img {
    width: 40px;
    cursor: pointer;
}

td,
th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

tr:nth-child(even) {
    background-color: #dddddd;
}