div.header {
    min-height: 50vh;
    display: flex;
    flex-direction: row;
}

div.header div.head1 {
    background-image: url('../img/chef-1245676_640.jpg');

}

div.header div.head2 {
    background-image: url('../img/kitchen-5055083_640.jpg');
}

div.header div.head3 {
    background-image: url('../img/restaurant-2623071_640.jpg');
}

div.header div.head2 {
    text-align: center;
}

div.header div.head2 img {
    /* width: 20%; */
    padding: 5%;
    backdrop-filter: blur(10px);
    border-radius: 10px;
}

div.header div {
    background-size: cover;
    flex-basis: 33.3%;
    transition: .2s;
}

