@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 10px;
}

div.app {
  position: relative;
  min-height: 120vh;
  max-width: 1650px;
  margin: 0 auto;
}

main {
  /* background-color: palevioletred; */
  padding-bottom: 100px;
}
div.menuHead {
    display: flex;
    justify-content: space-around;
    margin: 5px 3%;
}

div.menuHead a {
    display: inline-block;
    font-size: 1.2rem;
    color: black;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 5px;
}

div.menuHead a:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

div.menuHead i {
    margin-right: 3px;
}

div.menuHead img.closeHamburger {
    display: none;
}

@media (max-width:500px) and (orientation:portrait) {
    div.menuHead {
        flex-direction: column;
    }
}



div.menuHead img {
    display: block;
    width: 20%;
    margin: 2% auto;
    /* transition: 1s ease 1s; */
}

@media (min-width:501px) {
    div.menuHead img {
        display: none;
    }
}

@media (max-width:500px) {
    div.menuHead a {
        display: none;
    }
}

div.menuHead img.hamburger:hover~a {
    display: inline-block;
    /* color: red; */
}
div.header {
    min-height: 50vh;
    display: flex;
    flex-direction: row;
}

div.header div.head1 {
    background-image: url(/01-2021/static/media/chef-1245676_640.095ac8f0.jpg);

}

div.header div.head2 {
    background-image: url(/01-2021/static/media/kitchen-5055083_640.614d77e7.jpg);
}

div.header div.head3 {
    background-image: url(/01-2021/static/media/restaurant-2623071_640.8b847fd5.jpg);
}

div.header div.head2 {
    text-align: center;
}

div.header div.head2 img {
    /* width: 20%; */
    padding: 5%;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    border-radius: 10px;
}

div.header div {
    background-size: cover;
    flex-basis: 33.3%;
    transition: .2s;
}

div.header div:hover {}
div.mainMenu ul {
    list-style: none;
    margin: 5px 5% 20px;
    height: 80px;
    text-align: center;
}

div.mainMenu ul li {
    display: inline-block;
    padding: 5px 3%;
    margin: 2px 2px;
    border: 2px solid white;
    background-color: white;
    transition: .3s;
}

div.mainMenu ul li a {
    font-size: 1.7rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: black;
}

div.mainMenu ul li a:hover {
    color: rgb(75, 73, 73);
}

div.mainMenu ul li:hover {
    border: 2px solid gray;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}
div.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: rgb(128, 128, 128);
    padding: 2% 0;
    margin-top: 10%;
}

div.footer p {
    font-size: 1.2rem;
    text-align: center;
}
div.home {
    text-align: center;
}

div.home img {
    width: 10%;
}

div.home p {
    font-size: 1.5rem;
}
div.products {
    display: flex;
    flex-direction: column;
    align-items: center;
}



div.products p {
    font-size: 1.5rem;
}
div.categories {
    /* background-color: yellowgreen; */
    padding: 10px;
}

div.categories ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

div.categories li {
    font-size: 1.5rem;
    padding: 10px;
    border: 1px solid black;
    margin: 10px;
    cursor: pointer;
}

div.categories ul li.active {
    font-weight: bolder;
}

div.categories li:hover {
    background-color: black;
    color: white;
}
div.productsList {
    width: 70%;
    padding: 10px;
    color: black;
}

div.productsList ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

div.productsList li {
    font-size: 1.5rem;
    padding: 5px;
    border: 1px solid black;
    margin: 10px;
    width: 25%;
    overflow: hidden;
    /* cursor: pointer; */
}

div.productsList li:hover {
    background-color: black;
    color: white;
}

div.productsList ul img {
    max-width: 10vh;
    /* margin-right: 20px; */
}

div.productsList ul button {
    font-size: 1.5rem;
    padding: 10px;
    /* max-height: 50px; */
    cursor: pointer;
}

div.productsList ul button:hover {
    background-color: lightgray;
}

div.productsList ul li p:last-child {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
div.productDetail {
    width: 70%;
    margin: 0 auto;
    font-size: 1.5rem;
}

div.productDetail img {
    width: 30%;
}
div.aboutUs {
    text-align: center;
}

div.aboutUs img {
    width: 10%;
}

div.aboutUs p {
    font-size: 1.5rem;
}

div.contact {
    text-align: center;
}

div.contact img {
    width: 10%;
}

div.contact p {
    font-size: 1.5rem;
}

div.cart {
    /* background-color: aquamarine; */
    max-width: 70%;
    margin: 0 auto;
    font-size: 1.5rem;
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

table img {
    width: 40px;
    cursor: pointer;
}

td,
th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

tr:nth-child(even) {
    background-color: #dddddd;
}
